        value: '吞咽困难'
      }],
      // 发病诱因选项（耳鼻喉相关）
      onsetFactors: [{
        label: '外感风寒（受凉后鼻塞咽痛）',
        value: '外感风寒'
      }, {
        label: '外感风热（发热伴咽痛流涕）',
        value: '外感风热'
      }, {
        label: '过敏因素（接触花粉、尘螨）',
        value: '过敏因素'
      }, {
        label: '饮食辛辣刺激',
        value: '饮食辛辣'
      }, {
        label: '熬夜劳累（免疫力下降）',
        value: '熬夜劳累'
      }, {
        label: '用嗓过度（教师、歌手等）',
        value: '用嗓过度'
      }, {
        label: '环境干燥/污染',
        value: '环境因素'
      }, {
