        value: '不明原因'
      }]
    };
  },
  methods: {
    // 验证输入信息是否完整
    validateInput() {
      // 基本验证，至少需要选择一些症状信息
      if (this.symptoms.lesionLocation.length === 0 && this.symptoms.lesionProperty.length === 0 && this.symptoms.performances.length === 0 && !this.symptoms.otherSymptoms) {
        ElMessage.warning('请至少提供一些症状信息');
        return false;
      }
      return true;
    },
    // 组合所有输入的数据
    combineData() {
      let data = [];

      // 添加患者基本信息
      if (this.patientInfo.name || this.patientInfo.gender || this.patientInfo.age) {
        data.push("患者基本信息：");
        if (this.patientInfo.name) data.push(`姓名：${this.patientInfo.name}`);
        if (this.patientInfo.gender) data.push(`性别：${this.patientInfo.gender}`);
        if (this.patientInfo.age) data.push(`年龄：${this.patientInfo.age}岁`);
        data.push("");
      }

      // 添加症状信息
      data.push("症状特点：");
      if (this.symptoms.lesionLocation.length > 0) {
        data.push(`病变部位：${this.symptoms.lesionLocation.join('、')}`);
      }
      if (this.symptoms.lesionProperty.length > 0) {
        data.push(`症状性质：${this.symptoms.lesionProperty.join('、')}`);
      }
      if (this.symptoms.performances.length > 0) {
        data.push(`症状表现：${this.symptoms.performances.join('、')}`);
      }
      if (this.symptoms.otherSymptoms) {
        data.push(`其他症状：${this.symptoms.otherSymptoms}`);
      }
      data.push("");

      // 添加发病情况
      data.push("发病情况：");
      if (this.onset.situation) data.push(`发病情况：${this.onset.situation}`);
      if (this.onset.duration) data.push(`持续时间：${this.onset.duration}`);
      if (this.onset.factors.length > 0) {
        data.push(`可能诱因：${this.onset.factors.join('、')}`);
      }
      if (this.onset.otherFactors) {
        data.push(`其他诱因：${this.onset.otherFactors}`);
      }
      data.push("");

      // 添加诊疗经过
      if (this.treatment.previousDiagnosis || this.treatment.previousTreatment || this.treatment.treatmentEffect) {
        data.push("诊疗经过：");
        if (this.treatment.previousDiagnosis) data.push(`既往诊断：${this.treatment.previousDiagnosis}`);
        if (this.treatment.previousTreatment) data.push(`曾接受的治疗：${this.treatment.previousTreatment}`);
        if (this.treatment.treatmentEffect) data.push(`治疗效果：${this.treatment.treatmentEffect}`);
      }
      return data.join("\n");
    },
    // 生成分析（保持原有功能不变）
    async generateAnalysis() {
      // 验证输入
      if (!this.validateInput()) {
        return;
      }
      this.loading = true;
      try {
        // 组合数据
        const patientData = this.combineData();
        // 构建提示信息
        let prompt = "我需要你使用中医耳鼻喉科的知识对我的症状进行分析，这是信息：\n";
        let msg = prompt + patientData;
        const encodedDescription = encodeURIComponent(msg);

        // 尝试导入数据传输工具
        import('@/lin/util/data-transfer').then(({
          saveData,
          generateUniqueId
        }) => {
          // 生成唯一ID
          const dataId = generateUniqueId();
          let infos = msg;
          // 存储数据到localStorage
          const saved = saveData(dataId, infos);
          console.log(dataId);
          if (saved) {
            // 传递数据ID作为参数
            try {
              const newWindow = window.open(`/#/chat?data_id=${dataId}`);
              if (!newWindow || newWindow.closed || typeof newWindow.closed === 'undefined') {
                // 如果window.open失败，使用location.href
                window.location.href = `/#/chat?data_id=${dataId}`;
              }
            } catch (error) {
              // 捕获任何可能的错误，使用location.href作为后备方案
              window.location.href = `/#/chat?data_id=${dataId}`;
            }
          }
        }).catch(error => {
          console.error('加载数据传输工具失败:', error);
          // 如果导入失败，回退到原来的方式
          window.location.href = `/#/chat?user_input=${encodedDescription}`;
        });
      } catch (error) {
        ElMessage.error('生成分析信息时出错');
        console.error(error);
      } finally {
        this.loading = false;
      }
    }
  }
};